import * as THREE from "three";
import Terrain from './Terrain';

export default class SceneController
{
    constructor()
    {
        this.scene = new THREE.Scene();
  	    this.renderer = new THREE.WebGLRenderer( { antialias: true } );
        this.clock = new THREE.Clock();
    }

    setup()
    {
        this.colourArr = (0, coloursToArray)('https://coolors.co/2d3047-93b7be-8d7d16-a799b7-048a81');//('https://coolors.co/0a191f-dad2d8-0f8b8d-ce8012-a8201a');
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        //this.renderer.setSize(500, 300);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        document.body.appendChild(this.renderer.domElement);

        this.disposed = false;
        this.scene.background = this.colourArr[3]; //this.colourArr[2]; 
        this.scene.fog = new THREE.Fog(this.colourArr[1], 5, 200); ////this.colourArr[4]; 
        this.delta = this.clock.getDelta();

        this.setupCamera();
        this.setupTerrain();
    };

    setupCamera()
    {
        let VIEW_ANGLE = 50;
        let ASPECT_RATIO = window.innerWidth / window.innerHeight;
        let NEAR_PLANE = 1;
        let FAR_PLANE = 1000;

        this.camera = new THREE.PerspectiveCamera(VIEW_ANGLE, ASPECT_RATIO, NEAR_PLANE, FAR_PLANE );
        //this.camera.position.set(-3, 6, 19);
        this.camera.position.set(0, 3, 20);
        this.camera.lookAt(new THREE.Vector3(0, 0, 0));
    };

    setupLight()
    {
        let hemiLight = new THREE.HemisphereLight(this.colourArr[1], this.colourArr[2], 1);
        this.scene.add(hemiLight);
    };

    setupTerrain()
    {
        this.terrain = new Terrain(this.scene);
        this.terrain.setup();
        this.terrain.setupTextures();
    };

    update() 
    {
        if(this.disposed) return;

        if (isNaN(this.delta) || this.delta > 1000 || this.delta === 0 ) 
        {
	        this.delta = 1000/60;
        }
        this.terrain.update(this.delta * 0.001);
        this.renderer.render(this.scene, this.camera);
    };

    onWindowResize()
    {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize( window.innerWidth, window.innerHeight);
    };

    dispose()
    {
        this.disposed = true;
        this.scene.children.forEach(child => {
            if(child.isMesh)
            {
                child.material.dispose();
                child.geometry.dispose();
            }  
        });
        this.scene.dispose();
        this.renderer.dispose();
        document.body.removeChild(this.renderer.domElement);
    };
} 

function coloursToArray(str) 
{
    return str.split('/').pop().split('-').map(function (hex) 
    {
        return new THREE.Color('#' + hex);
    });
};
